* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 50px;
    color: whitesmoke;
}
.container {
    text-align: center;
    vertical-align: middle;
    width: 100%;
    padding: 10px;
    font-size: 0;
}
.sidebar {
    margin-bottom: 10px;
    height: 500px;
    width: auto;
    background-color: #454140;
    position: relative;
    h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
section {
    height: 500px;
    width: auto;
    background-color: #36486b;
    position: relative;
    h1 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
@media (min-width: 680px) {
    .sidebar {
        font-size: 1rem;
        margin: 0;
        height: 500px;
        width: 20%;
        background-color: #454140;
        display: inline-block;

    }
    section {
        font-size: 1rem;
        height: 500px;
        width: 80%;
        background-color: #36486b;
        display: inline-block;

    }
}
